import { Injectable } from '@angular/core';
import { FormArray, FormControl, FormGroup, Validators } from '@angular/forms';
import { Class } from './models/class';
import { Field } from './models/field';

@Injectable({
  providedIn: 'root'
})
export class UsformsService {

  constructor() { }

  field(field: Field | null = null): FormGroup {
    let form = new FormGroup({
      title: new FormControl(null, [Validators.required]),
      dataType: new FormControl(null, [Validators.required])
    });
    if (field != null) {
      form.patchValue(field);
    }
    return form;
  }

  class(_class: Class | null = null): FormGroup {
    let form = new FormGroup({
      title: new FormControl(null, [Validators.required]),
      fields: new FormArray([]),
    })

    if (_class != null) {
      form.patchValue(_class);
      _class.fields.forEach(field => {
        let _field = this.field(field);
        (form.get('fields') as FormArray).push(_field);
      })
    }

    return form;
  }

}
